}
.utility {
  /* Utility: */
  font-family: 'GTWalsheimRegular';
  font-size: 12px;
  color: #082842;
  letter-spacing: 0.5px;
  line-height: 24px;
}
p,
.p {
  /* Paragraph: */
  font-family: 'GTWalsheimRegular';
  font-size: 18px;
  color: #082842;
  letter-spacing: 0.5px;
  line-height: 24px;
}
p.large,
  .p.large {
    /* Paragraph Large: */
    font-family: 'GTWalsheimRegular';
    font-size: 24px;
    color: #082842;
    letter-spacing: 0.5px;
    line-height: 36px;
}
.button {
  /* Button: */
  font-family: 'Chrono-Bold';
  font-size: 14px;
  color: #082842;
  letter-spacing: 1.4px;
  line-height: 12px;
  text-transform: uppercase;
}
a,
.a {
  /* Text Link: */
  font-family: 'GTWalsheimRegular';
  font-size: 18px;
  color: #8494A1;
  letter-spacing: 0.5px;
  line-height: 24px;
  text-decoration: underline;
}
a.small,
  .a.small {
    /* Text Link Small: */
    font-family: 'GTWalsheimRegular';
    font-size: 12px;
    color: #8494A1;
    letter-spacing: 0.5px;
    line-height: 24px;
    text-decoration: underline;
}
